import ruetkitApi from '../apis/ruetkitApi'
import { toast } from 'react-toastify'
import { signin } from './signin'
import { navigate } from './navigate'

export const verify = ({ data, errorHandler, misc }) => async (dispatch, getState) => {
  const state = getState()
  try {
    const user = await ruetkitApi.post(`/api/users/${state.user.id}/verify/`, {
      code: data.code
    })

    /* handle successful user verification */
    if (user.status === 200) {
      // disable timer if has been set earlier
      misc.setNextResend(0)

      // set refresh and access token into localstorage
      localStorage.setItem('refreshToken', user.data.refreshToken)
      localStorage.setItem('accessToken', user.data.accessToken)
      const through = 'token'
      await dispatch(signin({ through }))
      dispatch(navigate('home'))
    }
  } catch (error) {
    misc.setProgress(false)
    if (error.response.status === 401) {
      if (error.response.data.error.field === 'code') {
        errorHandler.setCodeError(error.response.data.error.detail)
      }
    } else if (error.response.status === 429) {
      toast.error('You have exceeded maximum verification requests', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    } else {
      toast.error('Something went wrong', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }
}
