import {
  Alert,
  Autocomplete,
  Button,
  CircularProgress,
  Divider,
  FormControl,
  InputAdornment,
  LinearProgress,
  Stack,
  TextField,
  Typography,
  useTheme,
} from '@mui/material'

import { Box } from '@mui/system'
import React, { useCallback, useEffect, useState } from 'react'
// import { Editor } from "react-draft-wysiwyg";
// import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import { Add, Block, Check, CorporateFare, Title } from '@mui/icons-material'

// import {Editor, EditorState} from 'draft-js';
// import 'draft-js/dist/Draft.css';
import ClassIcon from '@mui/icons-material/Class'
import ReactQuill from 'react-quill' // ES6
import { useDropzone } from 'react-dropzone'
import { useDispatch, useSelector } from 'react-redux'
import { fetchDepartments } from '../../actions/fetchDepartments'
import { fetchCourses } from '../../actions/fetchCourses'
import { toast } from 'react-toastify'
import { addMaterialMeta } from '../../actions/addMaterialMeta'
import { UNSET_APP_PROGRESS } from '../../actions/types'
import { uploadMaterial } from '../../actions/uploadMaterial'
import { checkTitle } from '../../actions/checkTitle'
import { uploadThumbnail } from '../../actions/uploadThumbnail'

import PropTypes from 'prop-types'

function LinearProgressWithLabel (props) {
  return (
    <Box sx={{ display: 'flex', alignItems: 'center' }}>
      <Box sx={{ width: '100%', mr: 1 }}>
        <LinearProgress variant="determinate" {...props} />
      </Box>
      <Box sx={{ minWidth: 35 }}>
        <Typography variant="body2" color="text.secondary">{`${Math.round(
          props.value,
        )}%`}</Typography>
      </Box>
    </Box>
  )
}

LinearProgressWithLabel.propTypes = {
  /**
   * The value of the progress indicator for the determinate and buffer variants.
   * Value between 0 and 100.
   */
  value: PropTypes.number.isRequired,
}

export const AddMaterial = (callback, deps) => {
  const dispatch = useDispatch()
  const departments = useSelector(state => state.app.departments)
  const appProgress = useSelector(state => state.app.progress)

  const [departmentOptions, setDepartmentOptions] = useState([])
  const [showDepartmentOptionsProgress, setShowDepartmentOptionsProgress] = useState(true)

  const [courseOptions, setCourseOptions] = useState([])
  const [showCourseOptionsProgress, setShowCourseOptionProgress] = useState(true)

  const [selectedDepartment, setSelectedDepartment] = useState()
  const [selectedCourse, setSelectedCourse] = useState()

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState({ text: '' })

  const [titleError, setTitleError] = useState('')
  // const [descriptionError, setDescriptionError] = useState('')
  const [departmentError, setDepartmentError] = useState('')
  const [courseError, setCourseError] = useState('')
  const [thumbnailFileError, setThumbnailFileError] = useState('')
  const [materialFileError, setMaterialFileError] = useState('')

  const [isThumbnailUploading, setIsThumbnailUploading] = useState(false)

  const [uploadedThumbnailPercent, setUploadedThumbnailPercent] = useState(0)

  const [uploadedFileID, setUploadedFileID] = useState()
  const [uploadedThumbnailID, setUploadedThumbnailID] = useState()

  const [isMaterialUploading, setIsMaterialUploading] = useState(false)
  const [uploadedMaterialPercent, setUploadedMaterialPercent] = useState(0)

  const [isUploadedFileProcessing, setIsUploadedFileProcessing] = useState(false)

  const [debouncedTitle, setDebouncedTitle] = useState('')

  useEffect(() => {
    console.log(selectedDepartment)
  }, [selectedDepartment])

  useEffect(() => {
    console.log(selectedCourse)
  }, [selectedCourse])

  useEffect(() => {
    if (departments !== undefined) {
      const _departmentOptions = []
      departments.map((department, _index) => {
        _departmentOptions.push({ label: `${department.description} [${department.acronym}]`, id: department.id })
      })
      setDepartmentOptions(_departmentOptions)
    }
  }, [departments])

  useEffect(() => {
    setCourseOptions([])
    if (selectedDepartment !== null && selectedDepartment !== undefined) {
      setShowCourseOptionProgress(true)
      const data = {
        departmentId: selectedDepartment.id
      }
      const misc = {
        setShowCourseOptionProgress,
        setCourseOptions
      }
      dispatch(fetchCourses({ data, misc }))
    }
  }, [selectedDepartment])

  useEffect(() => {
    console.log(departmentOptions)
    dispatch({ type: UNSET_APP_PROGRESS })
    if (departments === undefined) {
      const misc = {
        setShowDepartmentOptionsProgress
      }
      dispatch(fetchDepartments({ misc }))
    } else {
      setShowDepartmentOptionsProgress(false)
    }
  }, [])

  const handleEditor = (value) => {
    setDescription({ text: value })
    console.log(value)
  }

  const onClickCourse = () => {
    if (selectedDepartment === undefined) {
      toast.warn('Please specify a department', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  // useEffect(() => {
  //   console.log(isUploadFormValid())
  // }, [title, description, selectedDepartment, selectedCourse])

  const handleTitle = (event) => {
    setTitle(event.target.value)
    if (event.target.value === '') {
      setTitleError('Title is required')
    } else {
      setTitleError('')
    }
  }

  const [isCheckingTitle, setIsCheckingTitle] = useState()
  const [isTitleAvailable, setIsTitleAvailable] = useState()

  useEffect(() => {
    if (title == '') {
      setIsTitleAvailable(undefined)
      return
    }
    const timerId = setTimeout(() => {
      setIsCheckingTitle(true)
      setDebouncedTitle(title)
    }, 300)
    return () => {
      clearTimeout(timerId)
    }
  }, [title])

  useEffect(() => {
    if (title === '') return
    console.log(debouncedTitle)
    setIsTitleAvailable(undefined)
    const data = {
      title
    }
    const misc = {
      setIsCheckingTitle,
      setIsTitleAvailable,
      setTitleError
    }

    dispatch(checkTitle({ data, misc }))
  }, [debouncedTitle])

  const handleDepartment = (event, value) => {
    setSelectedDepartment(value)
    if (value === null || value === undefined) {
      setDepartmentError('Department is required')
    } else {
      setDepartmentError('')
    }
  }

  const handleCourse = (event, value) => {
    setSelectedCourse(value)
    if (value === null || value === undefined) {
      setCourseError('Course is required')
    } else {
      setCourseError('')
    }
  }
  const [acceptedMaterialFile, setAcceptedMaterialFile] = useState()
  const [acceptedThumbnailFile, setAcceptedThumbnailFile] = useState()

  useEffect(() => {
    if (acceptedThumbnailFile !== undefined) {
      setThumbnailFileError('')

      console.log(acceptedThumbnailFile.name)
      setIsThumbnailUploading(true)

      const formData = new FormData()
      formData.append('image', acceptedThumbnailFile)

      const misc = {
        setIsThumbnailUploading,
        setUploadedThumbnailPercent,
        setUploadedThumbnailID
      }

      dispatch(uploadThumbnail({ data: formData, misc }))
    }
  }, [acceptedThumbnailFile])

  useEffect(() => {
    if (acceptedMaterialFile !== undefined) {
      setMaterialFileError('')

      console.log(acceptedMaterialFile)
      setIsMaterialUploading(true)

      const formData = new FormData()
      formData.append('file', acceptedMaterialFile)
      formData.append('title', title.length === 0 ? acceptedMaterialFile.name : title)

      const misc = {
        setIsMaterialUploading,
        setUploadedMaterialPercent,
        setUploadedFileID
      }

      dispatch(uploadMaterial({ data: formData, misc }))
    }
  }, [acceptedMaterialFile])

  useEffect(() => {
    if (uploadedMaterialPercent === 100) {
      setIsUploadedFileProcessing(true)
    }
  }, [uploadedMaterialPercent])

  const isUploadFormValid = () => {
    return (
      title.length !== 0 && titleError.length === 0 &&
      selectedDepartment !== undefined && selectedDepartment !== null &&
      selectedCourse !== undefined && selectedCourse !== null &&
      acceptedMaterialFile !== undefined && acceptedMaterialFile !== null &&
      acceptedThumbnailFile !== undefined && acceptedThumbnailFile !== null &&
      // more importantly
      uploadedThumbnailID !== undefined && uploadedThumbnailID !== null &&
      uploadedFileID !== undefined && uploadedFileID !== null
    )
  }

  const handleUpload = () => {
    if (!isUploadFormValid()) {
      if (title === '') {
        setTitleError('Title is required')
      }
      if (selectedDepartment === undefined || selectedDepartment === null) {
        setDepartmentError('Department is required')
      }
      if (selectedCourse === undefined || selectedCourse === null) {
        setCourseError('Course is required')
      }
      if (acceptedMaterialFile === undefined || uploadedFileID === undefined) {
        setMaterialFileError('Material is required')
      }
      if (acceptedThumbnailFile === undefined || uploadedThumbnailID === undefined) {
        setThumbnailFileError('Thumbnail is required')
      }
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
      return
    }

    const data = {
      title,
      description: description.text,
      course_id: selectedCourse.id,
      material_link_id: uploadedFileID,
      // thumbnail_link_id: uploadedThumbnailID
      thumbnail_link: uploadedThumbnailID
    }
    const misc = {
      setTitleError
    }
    dispatch(addMaterialMeta({ data, misc }))
  }
  const theme = useTheme()
  useEffect(() => {
    if (theme.palette.mode === 'dark') {
      console.log('dark')
      document.querySelector('.ql-editor').style.background = '#121212'

      setMaterialDropzoneBaseStyle({
        ...materialDropzoneBaseStyle,
        backgroundColor: theme.palette.mode === 'dark' ? '#121212' : '#fafafa'
      })
      setMaterialDropzoneBaseStyle({
        ...materialDropzoneBaseStyle,
        borderColor: theme.palette.mode === 'dark' ? '#d4d4d4' : 'gray'
      })
      // setMaterialDropzoneBaseStyle({...materialDropzoneBaseStyle, color: theme.palette.mode === 'dark' ? '#575757' : '#bdbdbd'})

    } else {
      document.querySelector('.ql-editor').style.background = '#fff'
    }
  }, [theme])
  const placeholderStyle = '.quill > .ql-container > .ql-editor.ql-blank::before{\n' +
    '    color: white;\n' +
    '}'

  // define acceptable thumbnail and material file types

  const acceptableThumbnailFileTypes = ['png', 'jpg', 'jpeg']
  const acceptableMaterialFileTypes = ['pdf', 'doc', 'docx']

  // <<--------------Start handling thumbnail file upload------------------->>

  const handleThumbnailDrop = useCallback(async (acceptedFiles) => {
    const allowedFileTypes = acceptableThumbnailFileTypes
    const fileType = acceptedFiles[0].name.split('.').slice(-1)[0]

    if (!allowedFileTypes.includes(fileType)) {
      setThumbnailFileError('File type not allowed')
      setThumbnailDropzoneStyle({
        ...thumbnailDropzoneBaseStyle,
        ...rejectStyle
      })
    } else {
      setThumbnailFileError('')
      setAcceptedThumbnailFile(acceptedFiles[0])
      setThumbnailDropzoneStyle({
        ...thumbnailDropzoneBaseStyle,
        ...acceptStyle
      })
    }

  })

  const handleThumbnailDrag = useCallback((event) => {
    console.log(event.dataTransfer.items[0].type)
    const allowedFileTypes = acceptableThumbnailFileTypes
    const fileType = event.dataTransfer.items[0].type.split('/').slice(-1)[0]

    if (!allowedFileTypes.includes(fileType)) {
      setThumbnailFileError('File type not allowed')
      setThumbnailDropzoneStyle({
        ...thumbnailDropzoneBaseStyle,
        ...rejectStyle
      })
    } else {
      console.log('accepted')
      setThumbnailFileError('')
      setThumbnailDropzoneStyle({
        ...thumbnailDropzoneBaseStyle,
        ...acceptStyle
      })
    }
  })

  const handleThumbnailDragLeave = useCallback((event) => {
    console.log('I was left')
    if (acceptedThumbnailFile === undefined) {
      setThumbnailFileError('Thumbnail is required')
      setThumbnailDropzoneStyle({ ...thumbnailDropzoneBaseStyle, ...rejectStyle })
    } else {
      setThumbnailFileError('')
    }
  })

  const {
    acceptedFiles: acceptedThumbnailFiles,
    getRootProps: getThumbnailDropzoneRootProps,
    getInputProps: getThumbnailDropzoneInputProps,
    isFocused: isThumbnailDropzoneFocused
  } = useDropzone({
    // accept: { 'application/pdf': [] },
    multiple: false,
    onDrop: handleThumbnailDrop,
    onDragEnter: handleThumbnailDrag,
    onDragLeave: handleThumbnailDragLeave
  })

  const [thumbnailDropzoneBaseStyle, setThumbnailDropzoneBaseStyle] = useState({
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 2,
    borderStyle: 'dashed',
    outline: 'none',
    transition: 'border .24s ease-in-out',
    color: '#bdbdbd'
  })

  const focusedStyle = {
    borderColor: '#2196f3'
  }

  const acceptStyle = {
    borderColor: '#00e676'
  }

  const rejectStyle = {
    borderColor: '#ff1744'
  }

  const [thumbnailDropzoneStyle, setThumbnailDropzoneStyle] = useState({
    ...thumbnailDropzoneBaseStyle
  })

  useEffect(() => {
    if (isThumbnailDropzoneFocused) {
      setThumbnailDropzoneStyle({ ...thumbnailDropzoneBaseStyle, ...focusedStyle })
    } else {
      setThumbnailDropzoneStyle({ ...thumbnailDropzoneBaseStyle })
    }
  }, [isThumbnailDropzoneFocused])

  // <<---------------Start handling material file upload------------------->>

  const handleMaterialDrop = useCallback(async (acceptedFiles) => {
    const allowedFileTypes = acceptableMaterialFileTypes
    const fileType = acceptedFiles[0].name.split('.').slice(-1)[0]

    if (!allowedFileTypes.includes(fileType)) {
      setMaterialFileError('File type not allowed')
      setMaterialDropzoneStyle({
        ...materialDropzoneBaseStyle,
        ...rejectStyle
      })
    } else {
      setMaterialFileError('')
      setAcceptedMaterialFile(acceptedFiles[0])
      setMaterialDropzoneStyle({
        ...materialDropzoneBaseStyle,
        ...acceptStyle
      })
    }

  })

  const handleMaterialDrag = useCallback((event) => {
    console.log(event.dataTransfer.items[0].type)
    const allowedFileTypes = acceptableMaterialFileTypes
    const fileType = event.dataTransfer.items[0].type.split('/').slice(-1)[0]

    if (!allowedFileTypes.includes(fileType)) {
      setMaterialFileError('File type not allowed')
      setMaterialDropzoneStyle({
        ...materialDropzoneBaseStyle,
        ...rejectStyle
      })
    } else {
      console.log('accepted')
      setMaterialFileError('')
      setMaterialDropzoneStyle({
        ...materialDropzoneBaseStyle,
        ...acceptStyle
      })
    }
  })

  const handleMaterialDragLeave = useCallback((event) => {
    console.log('I was left')
    if (acceptedMaterialFile === undefined) {
      setMaterialDropzoneStyle({ ...materialDropzoneBaseStyle, ...rejectStyle })
      setMaterialFileError('Material is required')
    } else {
      setMaterialFileError('')
    }
  })

  const {
    acceptedFiles: acceptedMaterialFiles,
    getRootProps: getMaterialDropzoneRootProps,
    getInputProps: getMaterialDropzoneInputProps,
    isFocused: isMaterialDropzoneFocused,
    isDragAccept: isMaterialDragAccept,
    isDragReject: isMaterialDragReject
  } = useDropzone({
    // accept: { 'application/pdf': [] },
    multiple: false,
    onDrop: handleMaterialDrop,
    onDragEnter: handleMaterialDrag,
    onDragLeave: handleMaterialDragLeave
  })

  const [materialDropzoneBaseStyle, setMaterialDropzoneBaseStyle] = useState({
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 2,
    borderStyle: 'dashed',
    outline: 'none',
    transition: 'border .24s ease-in-out',
    color: '#bdbdbd'
  })

  const [materialDropzoneStyle, setMaterialDropzoneStyle] = useState({
    ...materialDropzoneBaseStyle
  })

  useEffect(() => {
    if (isMaterialDropzoneFocused) {
      setMaterialDropzoneStyle({ ...materialDropzoneBaseStyle, ...focusedStyle })
    } else {
      setMaterialDropzoneStyle({ ...materialDropzoneBaseStyle })
    }
  }, [isMaterialDropzoneFocused])

  return (
    <Box flex={2} p={2}>
      {useTheme().palette.mode === 'dark' && <style>{placeholderStyle}</style>}
      <Stack justifyContent="space-between" width="100%">
        <Typography color="gray" variant="h5" sx={{ fontWeight: 500, m: 1 }}>
          Add a new material
        </Typography>
        <Divider/>
        {/*<Paper sx={{mt: 2}} elevation={1}>*/}
        <div>
          <FormControl>
            <TextField
              // disabled={open ? true : false}
              type="text"
              error={titleError.length == 0 ? false : true}
              helperText={titleError}
              value={title}
              // onChange={}
              sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}
              size="small"
              label="Title"
              variant="outlined"
              placeholder="What is your material title?"
              onChange={handleTitle}
              InputProps={{
                startAdornment:
                  <InputAdornment position="start">
                    <Title/>
                  </InputAdornment>,
                endAdornment:
                  <InputAdornment position="end">
                    {
                      isCheckingTitle && <CircularProgress size={'1.5rem'}/>
                    }
                    {
                      isTitleAvailable !== undefined && isTitleAvailable === true &&
                      <Check color="success"/>
                    }
                    {
                      isTitleAvailable !== undefined && isTitleAvailable === false &&
                      <Block color="error"/>
                    }
                  </InputAdornment>
              }}/>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ width: { xs: '50ch', sm: '65ch' }, mt: 2 }}>
            <ReactQuill
              onChange={handleEditor}
              style={{ border: '1px solid #c2c2c2', borderRadius: 2 }}
              placeholder="Type in a fancy description... (optional)"
              modules={{
                toolbar: [
                  [{ 'header': [1, 2, 3, 4, 5, false] }],
                  ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                  // [{'align': 'center'}],
                  [{ 'list': 'ordered' }, { 'list': 'bullet' }, { 'indent': '-1' }, { 'indent': '+1' }],
                  [{ 'color': [] }, { 'background': [] }],
                  ['link'],
                  ['clean']
                ],
              }}
              theme="bubble"/>
            <Typography align="right" variant="caption" color="gray">
              Select text for more options
            </Typography>
            <div style={{ textAlign: 'center' }} dangerouslySetInnerHTML={{ __html: description.text }}></div>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ mt: 2 }}>
            <Autocomplete
              size="small"
              disabled={showDepartmentOptionsProgress}
              sx={{ width: 400 }}
              isOptionEqualToValue={(option, value) => {
                return option.id === value.id
              }}
              renderInput={
                (params) =>
                  <TextField {...params} error={departmentError.length !== 0} helperText={departmentError}
                             label="Department" placeholder="Select department" InputProps={{
                    ...params.InputProps,
                    startAdornment:
                      <InputAdornment position="start">
                        <CorporateFare/>
                      </InputAdornment>
                  }}/>
              }
              options={departmentOptions}
              onChange={handleDepartment}
            />
            <Box sx={{ height: 2 }}>
              {showDepartmentOptionsProgress && <LinearProgress color="secondary"/>}
            </Box>
          </FormControl>
        </div>
        <div>
          <FormControl sx={{ mt: 2 }} onClick={onClickCourse}>
            <Autocomplete
              key={selectedDepartment}
              sx={{ width: 400 }}
              size="small"
              isOptionEqualToValue={(option, value) => {
                return option.id === value.id
              }}
              renderInput={
                (params) =>
                  <TextField
                    error={courseError.length !== 0}
                    helperText={courseError.length === 0 ? 'Select department to get courses' : 'Course is required'}
                    {...params}
                    label="Course"
                    placeholder="Select course"
                    InputProps={{
                      ...params.InputProps,
                      startAdornment:
                        <InputAdornment position="start">
                          <ClassIcon/>
                        </InputAdornment>
                    }}/>
              }
              options={courseOptions}
              onChange={handleCourse}
            />
            <Box sx={{ height: 2 }}>
              {selectedDepartment !== undefined && showCourseOptionsProgress && <LinearProgress color="secondary"/>}
            </Box>
          </FormControl>
        </div>
        {/*Thumbnail drag and drop zone */}
        {
          acceptedThumbnailFile === undefined &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Box>
                <Box {...getThumbnailDropzoneRootProps({ style: thumbnailDropzoneStyle })}>
                  <input {...getThumbnailDropzoneInputProps()} />
                  <p>Drag and drop your material thumbnail, or click to select file</p>
                </Box>
                <Typography align="left" sx={{ fontSize: '12px', color: '#f44336', mt: 1, ml: 2 }}>
                  {thumbnailFileError}
                </Typography>
                {/*<aside>*/}
                {/*  <h4>Files</h4>*/}
                {/*</aside>*/}
              </Box>
            </FormControl>
          </div>
        }

        {isThumbnailUploading &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Box sx={{ mt: '2%', height: '5%', mb: '2%' }}>
                <>
                  {/*<Typography>Uploading {acceptedThumbnailFile.name}</Typography>*/}
                  <Alert severity="info">
                    Uploading {acceptedThumbnailFile.name}
                  </Alert>
                  {/*<LinearProgress color="secondary" variant="determinate" value={uploadedThumbnailPercent}/> {uploadedThumbnailPercent}%*/}
                  <LinearProgressWithLabel value={uploadedThumbnailPercent}/>
                </>
              </Box>
            </FormControl>
          </div>
        }

        {
          uploadedThumbnailID !== undefined &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Alert severity="success">
                {acceptedThumbnailFile.name} has been uploaded.
              </Alert>
            </FormControl>
          </div>
        }

        {
          acceptedMaterialFile === undefined &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Box>
                <Box {...getMaterialDropzoneRootProps({ style: materialDropzoneStyle })}>
                  <input {...getMaterialDropzoneInputProps()} />
                  <p>Drag and drop your material here, or click to select file</p>
                </Box>
                <Typography align="left" sx={{ fontSize: '12px', color: '#f44336', mt: 1, ml: 2 }}>
                  {materialFileError}
                </Typography>
                {/*<aside>*/}
                {/*  <h4>Files</h4>*/}
                {/*</aside>*/}
              </Box>
            </FormControl>
          </div>
        }
        {isMaterialUploading &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Box sx={{ mt: '2%', height: '5%' }}>
                <>
                  {/*<Typography>Uploading {acceptedMaterialFile.name}</Typography>*/}
                  {
                    isUploadedFileProcessing ?
                      <>
                        <Alert severity="info">
                          Processing {acceptedMaterialFile.name} <br/>
                          <Divider/>
                          This might take a while.
                        </Alert>
                        <LinearProgress variant="indeterminate" color="secondary" />
                      </> :
                    <Alert severity="info">
                    Uploading {acceptedMaterialFile.name}
                    </Alert>
                  }
                  {/*<LinearProgress color="secondary" variant="indeterminate"/>*/}
                  {!isUploadedFileProcessing && <LinearProgressWithLabel value={uploadedMaterialPercent}/>}
                </>
              </Box>
            </FormControl>
          </div>
        }
        {
          uploadedFileID !== undefined &&
          <div>
            <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
              <Alert severity="success">
                {acceptedMaterialFile.name} has been uploaded.
                <Divider/>
                Click Add button to add your material
                to Ruetkit index.
              </Alert>
            </FormControl>
          </div>
        }
        <div style={{ marginTop: 30 }}>
          <FormControl sx={{ mt: 2, width: { xs: '50ch', sm: '65ch' } }}>
            <Button type="submit" variant="contained" color="primary" startIcon={<Add/>}
                    onClick={handleUpload}>Add</Button>
          </FormControl>
        </div>
        {/*</Paper>*/}
      </Stack>
    </Box>
  )
}
