import { SET_NAVIGATION, SIGNIN, SIGNOUT, VERIFY } from './types'
import ruetkitApi from '../apis/ruetkitApi'
import { toast } from 'react-toastify'
import { navigate } from './navigate'

export const signin = ({ data, errorHandler, misc, through }) => async (dispatch, getState) => {
  /*
  * status code and specific business logic
  * STATUS 200
  * OK; let the user proceed to app
  * STATUS 0
  * network error
  * STATUS 401
  * invalid credentials provided
  * STATUS 403
  * invalid credentials or restricted user
  * STATUS 425
  * unverified user
  * STATUS 429
  * exceeded sign in api endpoint throttling limit
  * */
  if (through === 'credentials') {
    try {
      const user = await ruetkitApi.post('/api/users/signin/', {
        ruet_id: data.ruetId, password: data.password
      })
      console.log(user)
      if (user.status === 200) {
        localStorage.setItem('refreshToken', user.data.refreshToken)
        localStorage.setItem('accessToken', user.data.accessToken)

        /*
            state.user might have ruet_id and password set from signin (unverified) and/or signup
            so clear them up and only set full_name
         */

        dispatch({ type: SET_NAVIGATION, payload: '/' })
        dispatch({ type: SIGNIN, payload: { fullname: user.data.fullname, role: user.data.role } })

        toast(`Welcome ${user.data.fullname}`, {
          position: 'bottom-left',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      }
    } catch (error) {
      // TODO remove console log
      console.log(error)
      if (error.response.status === 0) {
        toast.error(error.message, {
          position: 'bottom-left',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      } else if (error.response.status === 401) {
        // handle invalid credentials
        errorHandler.setPasswordError(error.response.data.error.detail)
      } else if (error.response.status === 403 || error.response.status === 404) {
        errorHandler.setRuetIdError(error.response.data.error.detail)
      } else if (error.response.status === 425) {
        // handle inactive accounts
        // if user is not verified (active) dispatch action with type VERIFY_LATER
        // also set `ruet_id` and `password` in payload as it would be used to sign in after verification
        // and finally dispatch navigate action creator with `/verify` route

        dispatch({
          type: VERIFY, payload: { id: error.response.data.error.id, isVerified: false }
        })
        dispatch(navigate('/verify'))

        // emit an error toast
        toast.warning(error.response.data.error.detail, {
          position: 'bottom-left',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      } else if (error.response.status === 429) {
        toast.error('You have exceeded maximum sign in requests. Try again after 24 hour.', {
          position: 'bottom-left',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      } else {
        toast.error('Something went wrong', {
          position: 'bottom-left',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      }
      if (misc) misc.setProgress(false)
      // if (errorHandler) {
      //     if (Array.isArray(error.response.data.error)) {
      //         for (let eachError of error.response.data.error) {
      //             if (eachError.field === 'ruet_id') errorHandler.setRuetIdError(eachError.detail)
      //         }
      //     }
      // }
    }
  } else if (through === 'token') {
    const accessToken = localStorage.getItem('accessToken')
    const refreshToken = localStorage.getItem('refreshToken')
    if (accessToken === null || refreshToken === null || accessToken === undefined || refreshToken === undefined) {
      return dispatch({ type: SIGNOUT })
    }
    try {
      // try verifying existing access token
      const accessTokenVerification = await ruetkitApi.post('/api/users/token/', {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`
        }
      })
      if (accessTokenVerification.status === 200) {
        dispatch({
          type: SIGNIN, payload: {
            fullname: accessTokenVerification.data.fullname,
            role: accessTokenVerification.data.role
          }
        })
        // TODO remove console log
        console.log('Access token ok')
        toast.success(`Welcome ${accessTokenVerification.data.fullname}`, {
          position: 'bottom-center',
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined
        })
      }
    } catch (error) {
      if (error.response.status === 401) {
        // access token is expired; try to refresh token
        try {
          const requestRefreshToken = await ruetkitApi.post('/api/users/token/refresh/', {
            refreshToken: refreshToken
          })
          if (requestRefreshToken.status === 200) {
            localStorage.setItem('accessToken', requestRefreshToken.data.accessToken)
            console.log('Setting new access token')
            // await verifyUser();
            dispatch({
              type: SIGNIN,
              payload: {
                full_name: requestRefreshToken.data.full_name
              }
            })
          }
        } catch (error) {
          localStorage.removeItem('accessToken')
          localStorage.removeItem('refreshToken')
          if (error.response.status === 401) {
            toast.error('You have been signed out', {
              position: 'bottom-left',
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            })
            dispatch({ type: SIGNOUT })
          }
        }
      } else if (error.response.status === 403) {
        dispatch({ type: SIGNOUT })
        localStorage.removeItem('accessToken')
        localStorage.removeItem('refreshToken')
        toast.error(error.response.data.error.detail, {
          position: 'bottom-left',
          autoClose: false,
          hideProgressBar: true,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      } else if (error.response.status === 429) {
        toast.error('You are misusing this website hence your device has been blocked temporarily for new requests', {
          position: 'bottom-left',
          autoClose: false,
          hideProgressBar: true,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      }
    }
  }
}