import { Backdrop, CircularProgress, CssBaseline } from '@mui/material'
import { createTheme, ThemeProvider } from '@mui/material/styles'
import Signup from './Signup'
import Signin from './Signin'
import { BrowserRouter, Route, Routes } from 'react-router-dom'

import { useDispatch, useSelector } from 'react-redux'
import { useEffect, useState } from 'react'
import { signin } from '../actions/signin'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import Verify from './Verify'
import RuetKitApp from './ruetkit/RuetKitApp'
import { RouteHandler } from './RouteHandler'
import ResetPasswordVerify from './ResetPasswordVerify'

const RuetKitBackDrop = (open = false) => {
  return (
    <Backdrop sx={{ color: '#fff', background: 'black', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open}>
      <CircularProgress color="inherit"/>
    </Backdrop>
  )
}

function App () {
  const dispatch = useDispatch()
  useEffect(() => {
    console.log(window.location.pathname)
    // dispatch(navigate(window.location.pathname))
  }, [])

  /*
  * Well...
  * A little bit of props drilling with a grain of salt
  *
  * Determine mode:
  * 1. Get system mode into systemMode
  * 2. If localstorage already have mode grab it otherwise pick the systemMode and pass to it onto `mode` state
  * 3. Create theme with `mode` palette
  * 4. Pass state `mode` and hook `setMode` to RuetKitApp
  * 5. Later on RuetKitApp will pass `mode` and `setMode` to SideBar
  * 6. Sidebar will check for what mode to use for the mode switch
  *   If `mode` is dark `checked` is true else `checked` is false
  *   mode switch will have a onChange listener (handleChange) and this listener will listen for `checked`
  *   if checked is true, setMode("dark") is called and other way round...
  *   along with `setMode`, `handleChange` saves mode to localStorage for next render with previously set mode
  *
  *  Question arises, why not using global state with redux?
  *  I was in a bad mood, nah mood, to reimplement action creator dispatcher stuff only for mode
  * */
  let systemMode = 'light'
  if (
    window.matchMedia &&
    window.matchMedia('(prefers-color-scheme: dark)').matches
  ) {
    systemMode = 'dark'
  }
  const [mode, setMode] = useState(localStorage.getItem('mode') || systemMode)

  let theme = createTheme({
    palette: {
      mode,
    },
  })

  useEffect(() => {
    dispatch(signin({ through: 'token' }))
  }, [])

  const isAuthenticated = useSelector(state => state.user.isAuthenticated)
  const DetectUser = (isAuthenticated) => {
    if (isAuthenticated === null) {
      return RuetKitBackDrop(true)
    } else if (isAuthenticated === false) {
      return (
        <Routes>
          <Route path="/" element={<Signin/>}/>
          <Route path="/signup" element={<Signup/>}/>
          <Route path="/verify" element={<Verify/>}/>
          <Route path="/reset" element={<ResetPasswordVerify/>}/>
          <Route path="*" element={<Signin/>}/>
        </Routes>
      )
    } else if (isAuthenticated === true) {
      return <RuetKitApp mode={mode} setMode={setMode}/>
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter>
        {/* <RuetKitAppBar /> */}
        <RouteHandler/>
        <CssBaseline/>
        {
          DetectUser(isAuthenticated)
        }
        <ToastContainer theme={mode}/>
      </BrowserRouter>
    </ThemeProvider>
  )
}

export default App
