import ruetkitApi from '../apis/ruetkitApi'
import { SET_DEPARTMENTS } from './types'

export const fetchDepartments = ({misc}) => async (dispatch, getState) => {
  try {
    const accessToken = localStorage.getItem('accessToken')
    const departments = await ruetkitApi.get('/api/departments', {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    console.log(departments.data)
    await dispatch({type: SET_DEPARTMENTS, payload: departments.data})
    misc.setShowDepartmentOptionsProgress(false)
  } catch (err) {

  }
}