import { SET_APP_PROGRESS, UNSET_APP_PROGRESS } from './types'
import ruetkitApi from '../apis/ruetkitApi'

export const uploadMaterial = ({data, misc}) => async (dispatch, getState) => {
  console.log(data)
  try {
    const accessToken = localStorage.getItem('accessToken')

    const uploadMaterial = await ruetkitApi.post('/api/materials/upload', data, {
      headers: {
        Authorization: `Bearer ${accessToken}`,
        "Content-Type": "multipart/form-data",
      },
      onUploadProgress: (progressEvent => {
        if (progressEvent.lengthComputable) {
          console.log(progressEvent.loaded + ' ' + progressEvent.total)
          misc.setUploadedMaterialPercent(Math.floor(progressEvent.loaded/progressEvent.total * 100))
        }
      })
    })
    console.log(uploadMaterial.data)
    misc.setIsMaterialUploading(false)
    misc.setUploadedFileID(uploadMaterial.data.id)
  } catch (err) {
    console.log(err)
  } finally {

  }
}