import axios from 'axios'

const baseUrl = window.location.hostname === 'parthokr.github.io' ||
  window.location.hostname === 'ruetkit.github.io' ||
  window.location.hostname === 'ruetkit.live'
  ? 'https://ruetkit-api.herokuapp.com' : 'http://localhost:8000'
export default axios.create({
    baseURL: baseUrl,
})