import {
  AccountBox,
  AccountCircle,
  AdminPanelSettings,
  CorporateFare,
  ExpandLess,
  ExpandMore,
  Home,
  MenuBook,
  ModeNightRounded,
  People,
  PrivacyTip,
  Settings,
  StackedLineChart
} from '@mui/icons-material'
import ClassIcon from '@mui/icons-material/Class'
import {
  Box,
  Collapse,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Paper,
  Skeleton,
  Switch,
  Typography,
} from '@mui/material'
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { navigate } from '../../actions/navigate'
import Divider from '@mui/material/Divider'
import { fetchSiteStat } from '../../actions/fetchSiteStat'

export const SideBar = ({ mode, setMode }) => {
  const [checked, setChecked] = useState(mode === 'dark')

  const handleChange = () => {
    setChecked(!checked)
  }

  useEffect(() => {
    if (checked) {
      setMode('dark')
      localStorage.setItem('mode', 'dark')
    } else {
      setMode('light')
      localStorage.setItem('mode', 'light')
    }
  }, [checked])
  const dispatch = useDispatch()
  const role = useSelector(state => state.user.role)
  const allowedRoleForPanel = ['STAFF', 'ADMIN']
  const handleClick = (to) => {
    dispatch(navigate(to))
  }

  const capitalize = (str) => {
    return str.toLowerCase().charAt(0).toUpperCase() + str.toLowerCase().slice(1)
  }

  const [isAdminOrStaffPanelExpanded, setIsAdminOrStaffPanelExpanded] = useState(false)
  const [isSettingsExpanded, setIsSettingsExpanded] = useState(false)

  const [stat, setStat] = useState()

  useEffect(() => {
    const misc = {
      setStat
    }
    dispatch(fetchSiteStat({misc}))
  }, [])

  return (
    <Box flex={0.7} p={2} sx={{ display: { xs: 'none', md: 'block' } }}>
      <Paper position="fixed" elevation={5}>
        <List>
          <ListItem disablePadding>
            <ListItemButton component="a" href="#" onClick={() => handleClick('home')}>
              <ListItemIcon>
                <Home/>
              </ListItemIcon>
              <ListItemText primary="Home"/>
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton component="a" href="#">
              <ListItemIcon>
                <People/>
              </ListItemIcon>
              <ListItemText primary="Acquaintances"/>
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton component="a" href="#" onClick={() => setIsSettingsExpanded(!isSettingsExpanded)}>
              <ListItemIcon>
                <Settings/>
              </ListItemIcon>
              <ListItemText primary="Settings"/>
              {isSettingsExpanded ? <ExpandMore/> : <ExpandLess/>}
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <Collapse in={isSettingsExpanded} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemIcon>
                    <AccountBox/>
                  </ListItemIcon>
                  <ListItemText primary="Profile"/>
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemIcon>
                    <PrivacyTip/>
                  </ListItemIcon>
                  <ListItemText primary="Privacy"/>
                </ListItemButton>
              </List>
            </Collapse>
          </ListItem>

          {
            allowedRoleForPanel.includes(role) &&
            <>
              <ListItem disablePadding>
                <ListItemButton onClick={() => setIsAdminOrStaffPanelExpanded(!isAdminOrStaffPanelExpanded)}>
                  <ListItemIcon>
                    <AdminPanelSettings/>
                  </ListItemIcon>
                  <ListItemText primary={`${capitalize(role)} Panel`}/>
                  {isAdminOrStaffPanelExpanded ? <ExpandMore/> : <ExpandLess/>}
                </ListItemButton>
              </ListItem>
              <ListItem disablePadding>
                <Collapse in={isAdminOrStaffPanelExpanded} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItemButton sx={{ pl: 4 }}>
                      <ListItemIcon>
                        <MenuBook/>
                      </ListItemIcon>
                      <ListItemText primary="Material"/>
                    </ListItemButton>
                    <ListItemButton sx={{ pl: 4 }}>
                      <ListItemIcon>
                        <ClassIcon/>
                      </ListItemIcon>
                      <ListItemText primary="Course"/>
                    </ListItemButton>
                    <ListItemButton sx={{ pl: 4 }}>
                      <ListItemIcon>
                        <CorporateFare/>
                      </ListItemIcon>
                      <ListItemText primary="Department"/>
                    </ListItemButton>
                    <ListItemButton sx={{ pl: 4 }}>
                      <ListItemIcon>
                        <AccountCircle/>
                      </ListItemIcon>
                      <ListItemText primary="User"/>
                    </ListItemButton>
                  </List>
                </Collapse>
              </ListItem>
            </>
          }
          <ListItem>
            {/*<ListItemButton>*/}
            <ListItemIcon>
              <ModeNightRounded/>
            </ListItemIcon>
            <Switch checked={checked} onChange={handleChange}/>
            {/*</ListItemButton>*/}
          </ListItem>
        </List>
        <Divider/>
        <Typography variant="h6" align="center" color="gray"><StackedLineChart/></Typography>
        {
          stat ?
            <Typography variant="subtitle2" align="center" color="gray">Materials: {stat.materials}</Typography> :
            <Skeleton animation="wave"/>
        }
        {
          stat ?
            <Typography variant="subtitle2" align="center" color="gray">Approved materials: {stat.approved_materials}</Typography> :
            <Skeleton animation="wave"/>
        }
        {
          stat ?
            <Typography variant="subtitle2" align="center" color="gray">Users: {stat.users}</Typography> :
            <Skeleton animation="wave"/>
        }
      </Paper>
    </Box>
  )
}
