import React, { useEffect, useState } from 'react'
import {
  Box,
  Button,
  ButtonGroup,
  FormControl,
  InputAdornment,
  LinearProgress,
  Paper,
  Stack,
  TextField,
  Typography,
} from '@mui/material'
import { ArrowBack, ArrowForward, Check, Key, LockReset, Numbers, Refresh } from '@mui/icons-material'
import { navigate } from '../actions/navigate'
import { useDispatch, useSelector } from 'react-redux'
import { toast } from 'react-toastify'
import { sendPasswordResetVerificationCode } from '../actions/sendPasswordResetVerificationCode'
import { verifyPasswordReset } from '../actions/verifyPasswordReset'
import { resetPassword } from '../actions/resetPassword'
import { resendPasswordVerificationCode } from '../actions/resendPasswordVerificationCode'

const ResetPasswordVerify = () => {
  const dispatch = useDispatch()

  const [open, setOpen] = useState(false)
  const [header, setHeader] = useState('Identify your account')

  const [id, setId] = useState('')

  const [ruetId, setRuetId] = useState('')
  const [code, setCode] = useState('')
  const [password, setPassword] = useState('')
  const [password2, setPassword2] = useState('')

  const [ruetIdError, setRuetIdError] = useState('')
  const [codeError, setCodeError] = useState('')
  const [passwordError, setPasswordError] = useState('')
  const [password2Error, setPassword2Error] = useState('')

  const [showRuetIdField, setShowRuetIdField] = useState(true)
  const [showCodeField, setShowCodeField] = useState(false)
  const [showPasswordFields, setShowPasswordFields] = useState(false)

  const handleRuetId = (event) => {
    setRuetId(event.target.value)
    if (event.target.value.length != 7) {
      setRuetIdError('Hmm isn\'t your ruet id 7 digits long?')
    } else {
      setRuetIdError('')
    }
  }

  const handleCode = (event) => {
    setCode(event.target.value)

    if (event.target.value.length !== 6) {
      setCodeError('This code should be 6 digits long')
    } else {
      setCodeError('')
    }
  }

  const handlePassword = (event) => {
    setPassword(event.target.value)

    if (event.target.value.length == 0) {
      setPasswordError('Hmm... ruetkit isn\'t smart enough to use a blank password ')
      return
    } else {
      setPasswordError('')
    }
    if (event.target.value.length < 4) {
      setPasswordError('This seems like a easy guess. Make it 8 characters long. ')
    } else {
      setPasswordError('')
    }
  }
  const handlePassword2 = (event) => {
    setPassword2(event.target.value)

    if (event.target.value !== password) {
      setPassword2Error('Hmm seems like you\'re going through a heavy time. But can you recheck the password for me? ')
    } else {
      setPassword2Error('')
    }
  }

  const onClickGoBack = () => {
    // navigate("/");
    dispatch(navigate('home'))
  }

  const isFormValid = (step) => {
    switch (step) {
      case 'pre':
        return (ruetIdError.length === 0 && ruetId.length !== 0)
      case 'mid':
        return (codeError.length === 0 && code.length !== 0)
      case 'final':
        return (passwordError.length === 0 && password.length !== 0 &&
          passwordError.length === 0 && password2.length &&
          password === password2
        )
    }
    return codeError.length === 0 && code.length !== 0
  }

  const handleContinue = () => {
    if (isFormValid('pre')) {
      setOpen(true)
      const data = {
        ruetId: ruetId
      }
      const errorHandler = {
        setRuetIdError
      }
      const misc = {
        setProgress: setOpen,
        setHeader,
        setShowRuetIdField,
        setShowCodeField,
        setId
      }
      dispatch(sendPasswordResetVerificationCode({ data, errorHandler, misc }))
    } else {
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  const handleVerify = () => {
    if (isFormValid('mid')) {
      setOpen(true)
      const data = {
        id,
        code
      }
      const errorHandler = {
        setCodeError
      }
      const misc = {
        setProgress: setOpen,
        setHeader,
        setShowCodeField,
        setShowPasswordFields
      }
      dispatch(verifyPasswordReset({ data, errorHandler, misc }))
    } else {
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  const handleReset = () => {
    if (isFormValid('final')) {
      setOpen(true)
      const data = {
        id,
        code,
        password,
        password2
      }
      const errorHandler = {
        setPasswordError,
        setPassword2Error
      }
      const misc = {
        setProgress: setOpen,
        setHeader,
        setShowPasswordFields
      }
      dispatch(resetPassword({ data, errorHandler, misc }))
    } else {
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  const [nextResend, setNextResend] = useState(0)
  const [start, setStart] = useState(false)
  const handleResend = () => {
    setOpen(true)
    const data = {
      id
    }
    const misc = {
      setProgress: setOpen,
      setStart
    }
    dispatch(resendPasswordVerificationCode({ data, misc }))
  }

  useEffect(() => {
    // console.log(nextResend);
  }, [nextResend])

  useEffect(() => {
    // console.log(start)
    if (start) {
      const timerId = setInterval(() => {
        setNextResend(oldResend => {
          return oldResend + 0.1
        })
      }, 100)
      return () => {
        setNextResend(0)
        clearInterval(timerId)
      }
    }
  }, [start])

  const user = useSelector(state => state.user)
  // const navigate = useNavigate();
  useEffect(() => {
    if (user.isAuthenticated) {
      dispatch(navigate('home'))
    }
  }, [user])

  return (
    <>
      <Stack direction="row" justifyContent="space-between" width="100%">
        <Box/>
        <Box padding={1} sx={{ marginTop: { xs: 0, md: 30 } }}>
          <Paper elevation={10}>
            <Box sx={{ height: 2 }}>
              {open && <LinearProgress color="primary"/>}
            </Box>
            <Typography textAlign="center" variant="h6" fontWeight={500} color="gray">{header}</Typography>
            {
              showRuetIdField &&
              <div>
                <FormControl>
                  <TextField
                    type="number"
                    disabled={open ? true : false}
                    error={ruetIdError.length == 0 ? false : true}
                    helperText={ruetIdError || 'Your ruet ID'}
                    value={ruetId}
                    onChange={handleRuetId}
                    sx={{ width: '50ch', m: 1 }}
                    label="Ruet ID"
                    variant="outlined"
                    placeholder="Ruet ID"
                    InputProps={{
                      startAdornment:
                        <InputAdornment position="start">
                          <Numbers/>
                        </InputAdornment>
                    }}/>
                </FormControl>
              </div>
            }
            {
              showCodeField &&
              <div>
                <FormControl>
                  <TextField
                    type="text"
                    disabled={open ? true : false}
                    error={codeError.length == 0 ? false : true}
                    helperText={codeError || 'Check your mail inbox'}
                    value={code}
                    onChange={handleCode}
                    onSubmit={handleCode}
                    sx={{ width: '50ch', m: 1 }}
                    label="Verification code"
                    variant="outlined"
                    placeholder="Verification code"
                    InputProps={{
                      startAdornment:
                        <InputAdornment position="start">
                          <Numbers/>
                        </InputAdornment>
                    }}/>
                </FormControl>
              </div>
            }
            {
              showPasswordFields &&
              <>
                <div>
                  <FormControl>
                    <TextField
                      type="text"
                      disabled={open ? true : false}
                      error={passwordError.length == 0 ? false : true}
                      helperText={passwordError}
                      value={password}
                      onChange={handlePassword}
                      sx={{ width: '50ch', m: 1 }}
                      label="New password"
                      variant="outlined"
                      placeholder="New password"
                      InputProps={{
                        startAdornment:
                          <InputAdornment position="start">
                            <Key/>
                          </InputAdornment>
                      }}/>
                  </FormControl>
                </div>
                <div>
                  <FormControl>
                    <TextField
                      type="text"
                      disabled={open ? true : false}
                      error={password2Error.length == 0 ? false : true}
                      helperText={password2Error}
                      value={password2}
                      onChange={handlePassword2}
                      sx={{ width: '50ch', m: 1 }}
                      label="Confirm password"
                      variant="outlined"
                      placeholder="Confirm password"
                      InputProps={{
                        startAdornment:
                          <InputAdornment position="start">
                            <Key/>
                          </InputAdornment>
                      }}/>
                  </FormControl>
                </div>
              </>
            }
            <div>
            </div>
            {/*<Divider />*/}
            <ButtonGroup disabled={open ? true : false} sx={{ m: 1 }} variant="outlined">
              <Button type="submit" color="primary" startIcon={<ArrowBack/>} onClick={onClickGoBack}>Go Back</Button>
              {
                showRuetIdField &&
                <Button type="submit" color="primary" endIcon={<ArrowForward/>}
                        onClick={handleContinue}>Continue</Button>
              }
              {
                showCodeField &&
                <>
                  <Button type="submit" color="primary" startIcon={<Check/>} onClick={handleVerify}>Verify</Button>
                  <Button type="submit" disabled={nextResend === 0 ? false : true} color="primary"
                          startIcon={<Refresh/>} onClick={handleResend}>Resend</Button>
                </>
              }
              {
                showPasswordFields &&
                <Button type="submit" color="primary" startIcon={<LockReset/>} onClick={handleReset}>Reset</Button>
              }
            </ButtonGroup>
          </Paper>
          <Box sx={{ height: 2 }}>
            {nextResend !== 0 && showCodeField &&
              <LinearProgress variant="determinate" value={nextResend / 30 * 100} color="info"/>}
          </Box>
        </Box>
        <Box/>
      </Stack>
    </>
  )
}

export default ResetPasswordVerify
