import React, { useState } from 'react'
import {
  Box,
  Button,
  ButtonGroup,
  FormControl,
  IconButton,
  InputAdornment,
  LinearProgress,
  Paper,
  Stack,
  TextField,
  Typography,
} from '@mui/material'
import {
  Add,
  AlternateEmail,
  ArrowBack,
  BrandingWatermark,
  Key,
  PermIdentity,
  Visibility,
  VisibilityOff
} from '@mui/icons-material'
import { useDispatch } from 'react-redux'
import { signup } from '../actions/signup'
import { toast } from 'react-toastify'
import { navigate } from '../actions/navigate'

const validateEmail = (email) => {
  return String(email)
    .toLowerCase()
    .match(
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    )
}

const Signup = () => {
  const dispatch = useDispatch()

  const [showPassword, setShowPassword] = useState(false)
  const [showPassword2, setShowPassword2] = useState(false)
  const [open, setOpen] = useState(false)

  // const navigate = useNavigate();

  const onClickSignin = () => {
    dispatch(navigate('home'))
  }

  const [fullname, setFullname] = useState('')
  const [ruetId, setRuetId] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [password2, setPassword2] = useState('')

  const [fullnameError, setFullnameError] = useState('')
  const [ruetIdError, setRuetIdError] = useState('')
  const [emailError, setEmailError] = useState('')
  const [passwordError, setPasswordError] = useState('')
  const [password2Error, setPassword2Error] = useState('')

  const handleFullname = (event) => {
    setFullname(event.target.value)

    if (event.target.value == '') {
      setFullnameError('Ruetkit can\'t detect anonymous people...')
    } else {
      setFullnameError('')
    }
  }
  const handleRuetId = (event) => {
    setRuetId(event.target.value)
    // TODO uncomment following
    // setEmail(event.target.value + "@student.ruet.ac.bd");

    if (event.target.value.length == 0) {
      setRuetIdError('Ruetkit is not ready for aliens yet ')
    } else if (event.target.value.length != 7) {
      setRuetIdError('If ruetkit remembers correctly, ruet ID is 7 digits long ')
    } else {
      setRuetIdError('')
    }
  }
  const handleEmail = (event) => {
    setEmail(event.target.value)

    if (event.target.value == '') {
      setEmailError('How can ruetkit deal with a blank email? ')
    } else if (!validateEmail(event.target.value)) {
      setEmailError('Woof woof... ruetkit detected invalid email ')
    } else {
      setEmailError('')
    }
  }
  const handlePassword = (event) => {
    setPassword(event.target.value)

    if (event.target.value.length == 0) {
      setPasswordError('Hmm... ruetkit isn\'t smart enough to use a blank password ')
      return
    } else {
      setPasswordError('')
    }
    if (event.target.value.length < 4) {
      setPasswordError('This seems like a easy guess. Make it 8 characters long. ')
    } else {
      setPasswordError('')
    }
  }
  const handlePassword2 = (event) => {
    setPassword2(event.target.value)

    if (event.target.value !== password) {
      setPassword2Error('Hmm seems like you\'re going through a heavy time. But can you recheck the password for me? ')
    } else {
      setPassword2Error('')
    }
  }

  const isFormValid = () => {
    return (
      fullnameError.length === 0 && fullname.length !== 0 &&
      ruetIdError.length === 0 && ruetId.length !== 0 &&
      emailError.length === 0 && email.length !== 0 &&
      passwordError.length === 0 && password.length !== 0 &&
      password2Error.length === 0 && password2.length !== 0
    )
  }

  const handleSignup = (event) => {
    event.preventDefault()

    if (isFormValid()) {
      setOpen(true)

      const data = {
        fullname,
        ruetId,
        email,
        password,
        password2,
        errorHandler: {
          setFullnameError,
          setRuetIdError,
          setEmailError,
          setPasswordError,
          setPassword2Error
        },
        setProgress: setOpen
      }
      dispatch(signup(data))
    } else {
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }

  // const user = useSelector(state => state.user)
  //
  // useEffect(() => {
  //     if (user.ruet_id !== undefined) {
  //         navigate('/verify')
  //     }
  // }, [user])

  return (
    <>
      <Stack direction="row" justifyContent="space-between" width="100%" sx={{ mt: { xs: 0, md: 20 } }}>
        <Box/>
        <Box padding={1}>
          <Paper elevation={10}>
            <Box sx={{ height: 2 }}>
              {open && <LinearProgress color="primary"/>}
            </Box>
            <Typography color="gray" textAlign="center" variant="h6" sx={{ fontWeight: 500, m: 1 }}>Join
              RuetKit</Typography>
            <div>
              <FormControl>
                <TextField
                  disabled={open ? true : false}
                  type="text"
                  error={fullnameError.length == 0 ? false : true}
                  helperText={fullnameError}
                  value={fullname}
                  onChange={handleFullname}
                  sx={{ width: '50ch', m: 1 }}
                  label="Full name"
                  variant="outlined"
                  placeholder="What is your full name?"
                  InputProps={{
                    startAdornment:
                      <InputAdornment position="start">
                        <PermIdentity/>
                      </InputAdornment>
                  }}/>
              </FormControl>
            </div>
            <div>
              <FormControl>
                <TextField
                  disabled={open ? true : false}
                  type="number"
                  error={ruetIdError.length == 0 ? false : true}
                  helperText={ruetIdError}
                  value={ruetId}
                  onChange={handleRuetId}
                  sx={{ width: '50ch', m: 1 }}
                  label="Ruet ID"
                  variant="outlined"
                  placeholder="What is your Ruet ID?"
                  InputProps={{
                    startAdornment:
                      <InputAdornment position="start">
                        <BrandingWatermark/>
                      </InputAdornment>
                  }}/>
              </FormControl>
            </div>
            <div>
              <TextField
                disabled={open ? true : false}
                type="email"
                error={emailError.length == 0 ? false : true}
                value={email}
                onChange={handleEmail}
                sx={{ width: '50ch', m: 1 }}
                label="Email"
                helperText={
                  emailError.length == 0 ?
                    <Typography variant="p">
                      *Prefer institutional email to verify account instantly.
                    </Typography> :
                    emailError
                }
                variant="outlined"
                placeholder="What is your email?"
                InputProps={{
                  startAdornment: <InputAdornment position="start">
                    <AlternateEmail/>
                  </InputAdornment>
                }}/>
            </div>
            <div>
              <TextField
                disabled={open ? true : false}
                type={showPassword ? 'text' : 'password'}
                value={password}
                onChange={handlePassword}
                error={passwordError.length == 0 ? false : true}
                helperText={passwordError}
                sx={{ width: '50ch', m: 1 }}
                label="Password"
                variant="outlined"
                placeholder="Create a strong password"
                InputProps={{
                  endAdornment:
                    <InputAdornment position="end">
                      <IconButton disabled={open ? true : false}
                                  onClick={(e) => setShowPassword(!showPassword)}>
                        {showPassword ? <Visibility/> : <VisibilityOff/>}
                      </IconButton>
                    </InputAdornment>,
                  startAdornment:
                    <InputAdornment position="start">
                      <Key/>
                    </InputAdornment>
                }}/>
            </div>
            <div>
              <TextField
                disabled={open ? true : false}
                type={showPassword2 ? 'text' : 'password'}
                error={password2Error.length == 0 ? false : true}
                helperText={password2Error}
                value={password2}
                onChange={handlePassword2}
                sx={{ width: '50ch', m: 1 }}
                label="Confirm password"
                variant="outlined"
                placeholder="Retype to confirm password"
                InputProps={{
                  endAdornment:
                    <InputAdornment position="end">
                      <IconButton disabled={open ? true : false}
                                  onClick={(e) => setShowPassword2(!showPassword2)}>
                        {showPassword2 ? <Visibility/> : <VisibilityOff/>}
                      </IconButton>
                    </InputAdornment>,
                  startAdornment:
                    <InputAdornment position="start">
                      <Key/>
                    </InputAdornment>
                }}/>
            </div>
            {/*<Divider />*/}
            <ButtonGroup disabled={open ? true : false} sx={{ m: 1 }} variant="outlined">
              <Button type="button" color="secondary" startIcon={<ArrowBack/>} onClick={onClickSignin}>I
                want to Sign
                in</Button>
              <Button type="submit" color="primary" startIcon={<Add/>} onClick={handleSignup}>Sign
                up</Button>
            </ButtonGroup>
          </Paper>
        </Box>
        <Box/>
      </Stack>
    </>
  )
}

export default Signup
