import ruetkitApi from '../apis/ruetkitApi'
import { SET_DEPARTMENTS } from './types'

export const fetchCourses = ({data, misc}) => async (dispatch, getState) => {
  try {
    const accessToken = localStorage.getItem('accessToken')
    const courses = await ruetkitApi.get(`/api/departments/${data.departmentId}/courses`, {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    misc.setShowCourseOptionProgress(false)

    const _courseOptions = []
    courses.data.map((course, _index) => {
      _courseOptions.push({ label: `${course.code} - ${course.title}`, id: course.id })
    })
    misc.setCourseOptions(_courseOptions)
  } catch (err) {

  }
}