import { SET_CODE, SET_USER_ID, SIGNIN, SIGNOUT, SIGNUP, VERIFY } from '../actions/types'

export const userReducer = (initialState = { isAuthenticated: null }, action) => {
  switch (action.type) {
    case SIGNIN:
      return { isAuthenticated: true, ...action.payload }
    case SIGNOUT:
      return { isAuthenticated: false }
    case SIGNUP:
      return { ...initialState, ...action.payload }
    case VERIFY:
      return { ...initialState, ...action.payload }
    case SET_USER_ID:
      return { ...initialState, ...action.payload }
    case SET_CODE:
      return { ...initialState, code: action.payload }
    default:
      return initialState
  }
}