import ruetkitApi from '../apis/ruetkitApi'

export const checkTitle = ({data, misc}) => async (dispatch, getState) => {
  try {
    const accessToken = localStorage.getItem('accessToken')
    const courses = await ruetkitApi.post(`/api/materials/check`, data, {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    misc.setIsCheckingTitle(false)
    misc.setIsTitleAvailable(true)
  } catch (err) {
    misc.setIsCheckingTitle(false)
    misc.setIsTitleAvailable(false)
    misc.setTitleError('This title is not available')
  }
}