import { Grid, } from '@mui/material'
import { Box } from '@mui/system'
import React, { useEffect, useState } from 'react'
import { MaterialCard } from './MaterialCard'
import { useDispatch } from 'react-redux'
import { fetchMaterials } from '../../actions/fetchMaterials'
import { setProgress } from '../../actions/appProgress'

export const Content = () => {
  const dispatch = useDispatch()
  const [materials, setMaterials] = useState(Array.from({ length: 9 }))
  useEffect(() => {
    dispatch(setProgress())
    const misc = {
      setMaterials
    }
    dispatch(fetchMaterials({ misc }))
  }, [])

  return (
    <Box flex={2} p={2}>
      <Grid container spacing={2}>
        {materials !== undefined && materials.map((material, _index) => {
          return (
            <Grid item xs={12} md={4}>
              <MaterialCard title={material && material.title}
                            thumbnailDriveFileID={material && material.thumbnail_link.url}
                            materialDriveFileID={material && material.material_link.drive_file_id}
                            uploadedBy={material && material.uploaded_by.fullname}/>
            </Grid>
          )
        })}
      </Grid>
    </Box>
  )
}
