import styled from '@emotion/styled'
import {
  alpha,
  AppBar,
  Avatar,
  Badge,
  Box,
  Button,
  InputBase,
  LinearProgress,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
} from '@mui/material'
import React, { useState } from 'react'
import PetsIcon from '@mui/icons-material/Pets'
import { Mail, Notifications } from '@mui/icons-material'
import SearchIcon from '@mui/icons-material/Search'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'

import { useDispatch, useSelector } from 'react-redux'
import { signout } from '../../actions/signout'

const StyledToolbar = styled(Toolbar)({
  display: 'flex',
  justifyContent: 'space-between'
})

const Logo = styled('div')(({ theme }) => ({
  color: theme.palette.mode === 'light' ? 'black' : 'white'
}))

const Search = styled('div')(({ theme }) => ({
  // padding: "0 10px",
  // backgroundColor: alpha(theme.palette.common.white, 0.15),
  // borderRadius: theme.shape.borderRadius,
  // width: "auto",
  border: '0.5px solid #ababab',
  color: theme.palette.mode === 'light' ? 'black' : 'white',
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: theme.palette.mode === 'light' ? alpha(theme.palette.common.white, 0.15) : alpha(theme.palette.common.black, 0.15),
  '&:hover': {
    backgroundColor: theme.palette.mode === 'light' ? alpha(theme.palette.common.white, 0.75) : alpha(theme.palette.common.black, 0.75),

  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: 'auto',
  },
}))

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}))

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '40ch',
      '&:focus': {
        width: '50ch',
      },
    },
    [theme.breakpoints.up('md')]: {
      width: '50ch',
      '&:focus': {
        width: '65ch',
      },
    },
  },
}))

const Icons = styled(Box)(({ theme }) => ({
  display: 'none',
  alignItems: 'center',
  gap: '20px',
  [theme.breakpoints.up('sm')]: {
    display: 'flex',
  },
}))

const UserBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  gap: '10px',
  [theme.breakpoints.up('sm')]: {
    display: 'none',
  },
}))

export const NavBar = () => {
  const [open, setOpen] = useState(false)
  const dispatch = useDispatch()

  const progress = useSelector(state => state.app.progress)
  const fullname = useSelector(state => state.user.full_name)

  const role = useSelector(state => state.user.role)

  const handleSignout = () => {
    dispatch(signout())
  }
  return (
    <>
      <AppBar position="sticky" style={{ backdropFilter: 'blur(20px)', background: 'transparent' }}>
        <StyledToolbar>
          <Logo>
            <Typography variant="h6" sx={{ display: { xs: 'none', sm: 'block' } }}>
              RuetKit
            </Typography>
          </Logo>
          <PetsIcon sx={{ display: { xs: 'block', sm: 'none' } }}/>
          {/*<Search>*/}
          {/*    <InputBase placeholder="Search..." sx={{ color: "black" }} />*/}
          {/*</Search>*/}
          <Search>
            <SearchIconWrapper>
              <SearchIcon/>
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
          <Icons>
            <Button>
              <Badge badgeContent={4} color="error">
                <Mail/>
              </Badge>
            </Button>
            <Button>
              <Badge badgeContent={4} color="error">
                <Notifications/>
              </Badge>
            </Button>
            <Button onClick={(e) => setOpen(true)}>
              <Avatar
                sx={{ width: '300', height: '300', display: 'flex' }}
                src="https://i.pravatar.cc/300"
              />
              {/*<AccountCircleIcon />*/}
            </Button>
          </Icons>
          <UserBox onClick={(e) => setOpen(true)}>
            <Avatar
              sx={{ width: '300', height: '300', display: 'flex' }}
              src="https://i.pravatar.cc/300"
            />
            <AccountCircleIcon/>
            <Typography variant="span">{fullname}</Typography>
          </UserBox>
        </StyledToolbar>
        <Menu
          id="demo-positioned-menu"
          aria-labelledby="demo-positioned-button"
          open={open}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          onClose={(e) => setOpen(false)}
        >
          <MenuItem>Profile</MenuItem>
          <MenuItem>My account</MenuItem>
          <MenuItem onClick={handleSignout}>Sign out</MenuItem>
        </Menu>
        <Box height={2} sx={{ opacity: 0.9 }}>
          {progress && <LinearProgress color="primary" sx={{ height: 3 }}/>}
        </Box>
      </AppBar>
    </>
  )
}
