import ruetkitApi from "../apis/ruetkitApi";
import {toast} from "react-toastify";
import {SET_CODE, SET_NAVIGATION, SIGNIN, VERIFY_LATER} from "./types";
import {signin} from "./signin";

export const verifyPasswordReset = ({data, errorHandler, misc}) => async (dispatch, getState) => {
    try {
        const user = await ruetkitApi.post(`/api/users/${data.id}/password/reset/verify`, {
            code: data.code
        });

        /* handle successful user verification */
        if (user.status === 200) {
            misc.setProgress(false);
            misc.setHeader("Reset your password");
            misc.setShowCodeField(false);
            misc.setShowPasswordFields(true);


            /*
                Fun fact: try using user.data.code :/
                haha
                such a breach
                don't dare to expect code from a verification endpoint
                this only responds 200 if ok
             */
            // dispatch({type: SET_CODE, payload: data.code});

            /*
            TODO
            * set refresh and access token into localstorage
            * If user skips to set password they will have access to account
            * */
            // localStorage.setItem("refresh", user.data.refresh);
            // localStorage.setItem("access", user.data.access);

            // const state = getState();
            // const data = {
            //     ruetId: state.user.ruet_id,
            //     password: Number(state.user.password)
            // }
            // const through = "token";
            // dispatch(signin({data, through}))
        }
    } catch (error) {
        if (error.response.status === 401) {
            errorHandler.setCodeError(error.response.data.error.detail);
        } else if (error.response.status === 429) {
            toast.error('You have exceeded maximum verification requests', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            toast.error('Something went wrong', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    } finally {
        misc.setProgress(false);
    }
}
