import { SET_APP_PROGRESS, UNSET_APP_PROGRESS } from './types'
import ruetkitApi from '../apis/ruetkitApi'
import axios from 'axios'

export const uploadThumbnail = ({data, misc}) => async (dispatch, getState) => {
  console.log(data)
  try {
    const accessToken = localStorage.getItem('accessToken')

    // const uploadThumbnail = await ruetkitApi.post('/api/materials/thumbnails/upload', data, {
    //   headers: {
    //     Authorization: `Bearer ${accessToken}`,
    //     "Content-Type": "multipart/form-data",
    //   }
    // })

    const uploadThumbnail = await axios.post('https://api.imgbb.com/1/upload?key=a5758a8b347e2b637a1e9c611d7cc65c', data, {
      onUploadProgress: (progressEvent => {
        if (progressEvent.lengthComputable) {
          console.log(progressEvent.loaded + ' ' + progressEvent.total)
          misc.setUploadedThumbnailPercent(Math.floor(progressEvent.loaded/progressEvent.total * 100))
        }
      })
    })

    console.log(uploadThumbnail.data.data)
    misc.setIsThumbnailUploading(false)
    misc.setUploadedThumbnailID(uploadThumbnail.data.data.medium.url)
  } catch (err) {
    console.log(err)
  } finally {

  }
}