import { VERIFY } from './types'
import ruetkitApi from '../apis/ruetkitApi'
import { toast } from 'react-toastify'
import { navigate } from './navigate'

export const signup = ({
  fullname,
  ruetId: ruet_id,
  email,
  password,
  password2,
  errorHandler,
  setProgress
}) => async (dispatch, getState) => {

  try {
    const user = await ruetkitApi.post('/api/users/signup/', {
      fullname,
      ruet_id,
      email,
      password,
      password2
    })
    // TODO remove console log
    console.log(user)
    dispatch({ type: VERIFY, payload: { id: user.data.id, isVerified: false } })
    dispatch(navigate('/verify'))
  } catch (error) {
    setProgress(false)
    // TODO remove console log
    console.log(error.response)
    for (let field in error.response.data) {
      if (field === 'ruet_id') errorHandler.setRuetIdError(error.response.data[field])
      if (field === 'full_name') errorHandler.setFullnameError(error.response.data[field])
      if (field === 'email') errorHandler.setEmailError(error.response.data[field])
      if (field === 'password') errorHandler.setPasswordError(error.response.data[field])
    }
    if (error.response.status === 429) {
      toast('You have exceeded maximum sign up requests. Try again after 24 hours.', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
      return
    }
    toast(`You have ${error.response.headers['ratelimit-remaining']} requests left`, {
      position: 'bottom-left',
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    })
  }
}