import { ThumbUpAlt, ThumbUpOffAlt } from '@mui/icons-material'
import ShareIcon from '@mui/icons-material/Share'
import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Checkbox,
  IconButton,
  Skeleton,
  Typography,
} from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux'

import thumbsUp from './sounds/thumbsUp.mp3'
import { toast } from 'react-toastify'

export const MaterialCard = ({ title, materialDriveFileID, thumbnailDriveFileID, uploadedBy }) => {
  const appProgress = useSelector(state => state.app.progress)
  const handleThumbsUp = () => {
    const sound = new Audio(thumbsUp)
    sound.play()
  }
  const handleCardClick = (materialDriveFileID) => {
    const url = `https://drive.google.com/u/1/uc?id=${materialDriveFileID}&export=download`
    const anchor = document.createElement('a')
    anchor.href = url
    document.body.appendChild(anchor)
    anchor.click()
    document.body.removeChild(anchor)

    toast.info('Download has been queued and will be started soon ', {
      position: 'bottom-left',
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    })
  }
  return (
    <Card sx={{height: {xs: 200, md:250, xl: 300}, width: {xs: 260}, position: "relative"}} elevation={3} key={materialDriveFileID}>
      <CardActionArea {...(materialDriveFileID && {onClick: () => handleCardClick(materialDriveFileID)})}>
        {
          appProgress ?
            <Skeleton
              variant="rectangular"
              animation="wave"
              width="auto"
            /> :
            <CardMedia
              component="img"
              width="auto"
              height="auto"
              style={{maxWidth: "260px", maxHeight: "300px", objectFit: "cover"}}
              image={thumbnailDriveFileID}
              alt={title}
            />
        }
        <CardContent>
          <Typography noWrap gutterBottom variant="span" component="div">
            {
              appProgress ?
                <Skeleton
                  animation="wave"
                /> : title
            }
          </Typography>
          <Typography sx={{fontSize: '.75rem', fontWeight: 500}} color="text.secondary">
            {
              appProgress ?
                <Skeleton
                  animation="wave"
                /> : `By ${uploadedBy}`
            }
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions disableSpacing>
        {/* <IconButton aria-label="add to favorites">
            <FavoriteIcon />
          </IconButton> */}
        {
          appProgress ?
            <Skeleton
              variant="circular"
              width={40}
              height={40}
            /> : <Checkbox
              onClick={handleThumbsUp}
              icon={<ThumbUpOffAlt/>}
              checkedIcon={<ThumbUpAlt sx={{ color: '#04a2e0' }}/>}
            />
        }
        {
          appProgress ?
            <Skeleton
              sx={{ marginLeft: 2 }}
              variant="circular"
              width={40}
              height={40}
            /> : <IconButton aria-label="share">
              <ShareIcon/>
            </IconButton>
        }
      </CardActions>
    </Card>
  )
}
