import ruetkitApi from '../apis/ruetkitApi'
import { SET_MATERIALS, UNSET_APP_PROGRESS } from './types'

export const fetchMaterials = ({misc}) => async (dispatch, getState) => {
  try {
    const accessToken = localStorage.getItem('accessToken')
    const materials = await ruetkitApi.get(`/api/materials`, {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    console.log(materials.data)
    misc.setMaterials(materials.data)
    dispatch({type: UNSET_APP_PROGRESS})
  } catch (err) {

  }
}