import React, {useState} from "react";
import {
    Backdrop,
    Box,
    Button,
    ButtonGroup, CircularProgress, Divider,
    FormControl, Grid,
    IconButton,
    InputAdornment, LinearProgress,
    Paper,
    Stack,
    TextField,
    Typography,
    Link as MUILink
} from "@mui/material";
import {ArrowForward, BrandingWatermark, Key, Login, Visibility, VisibilityOff} from "@mui/icons-material";
import {Link, useNavigate} from "react-router-dom";
import {toast} from "react-toastify";
import {useDispatch, useSelector} from "react-redux";
import {signin} from "../actions/signin";
import {useEffect} from "react";
import {navigate} from "../actions/navigate";

const Signup = () => {
    const [showPassword, setShowPassword] = useState(false);
    const [open, setOpen] = useState(false);

    const [ruetId, setRuetId] = useState("");
    const [password, setPassword] = useState("");

    const [ruetIdError, setRuetIdError] = useState("");
    const [passwordError, setPasswordError] = useState("");

    const handleRuetId = (event) => {
        setRuetId(event.target.value);
        if (Number.isNaN(Number(event.target.value))) {
            setRuetIdError("Invalid ruet ID")
        }
        else if (event.target.value.length === 0) {
            setRuetIdError("It seems like an alien's identifier ")
        } else if (event.target.value.length !== 7) {
            setRuetIdError("Isn't it 7 digits long?")
        }
        else {
            setRuetIdError("");
        }
    }

    const handlePassword = (event) => {
        setPassword(event.target.value);
        if (event.target.value.length === 0) {
            setPasswordError("It seems like an alien's password ")
        } else if (event.target.value.length < 4) {
            setPasswordError("You must had provided 4 characters long password")
        }
        else {
            setPasswordError("");
        }
    }

    const isFormValid = () => {
        return (
          ruetIdError.length === 0 && ruetId.length !== 0 &&
          passwordError.length === 0 && password.length !== 0
        );
    }

    const dispatch = useDispatch();
    const handleSignin = (event) => {
        event.preventDefault();
        if (isFormValid()) {
            setOpen(true);

            const data = {
                ruetId: parseInt(ruetId),
                password,
                setProgress: setOpen
            }
            const errorHandler = {
                    setRuetIdError,
                    setPasswordError
            }

            const misc = {
                setProgress: setOpen
            }

            const through = "credentials";
            dispatch(signin({data, errorHandler, misc, through}));
        } else {
            toast.error('Please recheck the fields', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    }

    // const navigate = useNavigate();
    const onClickSignup = () => {
        dispatch(navigate("/signup"));
    }

    const onClickLostPassword = () => {
        dispatch(navigate("/reset"));
    }

    const user = useSelector(state => state.user);
    useEffect(() => {
        if (user.ruet_id !== undefined) {
            navigate('/verify')
        }
    }, [user])

    return (
        <>
            <Stack justifyContent="space-between" alignItems="center" direction="row">
                <Box />
                <Box padding={1} sx={{marginTop: {xs: 0, md: 30}}}>
                    <Paper elevation={10}>
                        <Box sx={{ height: 2}}>
                            {open && <LinearProgress color="primary" />}
                        </Box>
                        <Typography textAlign="center" variant="h5" sx={{fontWeight: 500, color: "gray", m: 1}}>Sign in to
                            RuetKit</Typography>
                        <Divider />
                        <form onSubmit={handleSignin}>
                            <div style={{marginTop: 5}}>
                                <FormControl>
                                    <TextField
                                        disabled={open ? true : false}
                                        type="number"
                                        value={ruetId}
                                        onChange={handleRuetId}
                                        error={ruetIdError.length === 0 ? false : true}
                                        helperText={ruetIdError}
                                        sx={{width: "50ch", m: 1}}
                                        label="Ruet ID"
                                        variant="outlined"
                                        placeholder="Ruet ID"
                                        InputProps={{
                                            startAdornment:
                                                <InputAdornment position="start">
                                                    <BrandingWatermark/>
                                                </InputAdornment>
                                        }}/>
                                </FormControl>
                            </div>
                            <div>
                                <TextField
                                    disabled={open ? true : false}
                                    value={password}
                                    onChange={handlePassword}
                                    error={passwordError.length === 0 ? false : true}
                                    helperText={
                                    <>
                                        <Typography variant="span">
                                            {passwordError}
                                        </Typography>
                                        {/*<Typography variant="span" fontWeight={400}>*/}
                                            {/*<Link to="/reset" style={{textDecoration: "none"}}>Lost password?</Link>*/}
                                            <MUILink to="#" sx={{"&:hover": {"cursor": "pointer"}}} component="a" underline="hover" onClick={onClickLostPassword}>
                                                <Typography align="right" sx={{fontSize: '.75rem'}}>Lost password?</Typography>
                                            </MUILink>
                                        {/*</Typography>*/}
                                    </>
                                    }
                                    type={showPassword ? "text" : "password"}
                                    sx={{width: '50ch', m: 1}}
                                    label="Password"
                                    variant="outlined"
                                    placeholder="Type your password"
                                    InputProps={{
                                        endAdornment:
                                            <InputAdornment position="end">
                                                <IconButton disabled={open ? true : false} onClick={(e) => setShowPassword(!showPassword)}>
                                                    {showPassword ? <Visibility/> : <VisibilityOff/>}
                                                </IconButton>
                                            </InputAdornment>,
                                        startAdornment:
                                            <InputAdornment position="start">
                                                <Key/>
                                            </InputAdornment>
                                    }}/>
                            </div>
                            {/*<Divider />*/}
                            <ButtonGroup sx={{m: 1}} variant="outlined">
                                <Button type="submit" disabled={open ? true : false} color="primary" startIcon={<Login/>} onClick={handleSignin}>Sign in</Button>
                                <Button disabled={open ? true : false} color="secondary" endIcon={<ArrowForward/>} onClick={onClickSignup}>I want to Sign
                                    up</Button>
                            </ButtonGroup>
                        </form>
                    </Paper>
                </Box>
                <Box />
            </Stack>
        </>
    );
};

export default Signup;
