import ruetkitApi from "../apis/ruetkitApi";
import { SET_CODE, SET_USER_ID } from './types'
import { toast } from 'react-toastify'

export const sendPasswordResetVerificationCode = ({data, errorHandler, misc}) => async (dispatch, getState) => {
    try {
        const code = await ruetkitApi.get(`/api/users/${data.ruetId}/password/reset/`);
        if (code.status === 200 || code.status === 208) {
            misc.setHeader("Verify your ownership")
            misc.setShowRuetIdField(false);
            misc.setShowCodeField(true);
            if (code.status === 208) {
                toast.info(code.data.detail, {
                    position: "bottom-left",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
            misc.setId(code.data.id)
        }
    } catch (e) {
        console.log(e);
        errorHandler.setRuetIdError(e.response.data.error.detail)
    } finally {
        misc.setProgress(false);
    }
}