import React, { useEffect, useState } from 'react'
import {
  Box,
  Button,
  ButtonGroup,
  FormControl,
  InputAdornment,
  LinearProgress,
  Paper,
  Stack,
  TextField,
  Typography,
} from '@mui/material'
import { Check, Numbers, Refresh } from '@mui/icons-material'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { verify } from '../actions/verify'
import { toast } from 'react-toastify'
import { resendVerificationCode } from '../actions/resendVerificationCode'

const Verify = () => {
  const dispatch = useDispatch()

  const [open, setOpen] = useState(false)
  const [code, setCode] = useState('')
  const [codeError, setCodeError] = useState('')

  const handleCode = (event) => {
    setCode(event.target.value)

    if (event.target.value.length !== 6) {
      setCodeError('This code should be 6 digits long')
    } else {
      setCodeError('')
    }
  }

  const onClickGoBack = () => {
    navigate('/')
  }

  const isFormValid = () => {
    return codeError.length === 0 && code.length !== 0
  }

  const handleVerify = () => {
    if (isFormValid()) {
      setOpen(true)
      const data = {
        code
      }
      const errorHandler = {
        setCodeError
      }
      const misc = {
        setProgress: setOpen,
        setNextResend
      }
      dispatch(verify({ data, errorHandler, misc }))
    } else {
      toast.error('Please recheck the fields', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }
  const [nextResend, setNextResend] = useState(0)
  const [start, setStart] = useState(false)
  const handleResend = () => {
    const misc = {
      setProgress: setOpen,
      setStart
    }
    dispatch(resendVerificationCode({ misc }))
  }

  useEffect(() => {
    // console.log(nextResend);
  }, [nextResend])

  useEffect(() => {
    // console.log(start)
    if (start) {
      const timerId = setInterval(() => {
        setNextResend(oldResend => {
          return oldResend + 0.1
        })
      }, 100)
      return () => {
        setNextResend(0)
        clearInterval(timerId)
      }
    }
  }, [start])

  const user = useSelector(state => state.user)
  const navigate = useNavigate()
  useEffect(() => {
    if (user.isVerified !== false) {
      navigate('/')
    }
  }, [user])

  return (
    <>
      <Stack direction="row" justifyContent="space-between" width="100%">
        <Box/>
        <Box padding={1} sx={{ marginTop: { xs: 0, md: 30 } }}>
          <Paper elevation={10}>
            <Box sx={{ height: 2 }}>
              {open && <LinearProgress color="primary"/>}
            </Box>
            <Typography textAlign="center" variant="h6" fontWeight={500} color="gray">Verify your account</Typography>
            <div>
              <FormControl>
                <TextField
                  type="text"
                  disabled={open ? true : false}
                  error={codeError.length == 0 ? false : true}
                  helperText={codeError || 'Check your mail inbox'}
                  value={code}
                  onChange={handleCode}
                  onSubmit={handleCode}
                  sx={{ width: '50ch', m: 1 }}
                  label="Verification code"
                  variant="outlined"
                  placeholder="Verification code"
                  InputProps={{
                    startAdornment:
                      <InputAdornment position="start">
                        <Numbers/>
                      </InputAdornment>
                  }}/>
              </FormControl>
            </div>
            <div>
            </div>
            {/*<Divider />*/}
            <ButtonGroup disabled={open ? true : false} sx={{ m: 1 }} variant="outlined">
              <Button type="submit" color="primary" startIcon={<Check/>} onClick={handleVerify}>Verify</Button>
              <Button type="submit" disabled={nextResend === 0 ? false : true} color="primary" startIcon={<Refresh/>}
                      onClick={handleResend}>Resend</Button>
            </ButtonGroup>
          </Paper>
          <Box sx={{ height: 2 }}>
            {nextResend !== 0 && <LinearProgress variant="determinate" value={nextResend / 30 * 100} color="info"/>}
          </Box>
        </Box>
        <Box/>
      </Stack>
    </>
  )
}

export default Verify
