import {useSelector} from "react-redux";
import {useEffect} from "react";
import {useNavigate} from "react-router-dom";

export const RouteHandler = () => {
    const navigate = useNavigate();
    const navigation = useSelector(state => state.app.navigate)
    useEffect(() => {
        if (navigation === "/") return;
        if (navigation === "home") return navigate("/");
        if (navigation === "settings") return navigate("/settings");
        if (navigation === "add-material")  return navigate('/material/add')
        console.log(navigation)
        navigate(navigation);
    }, [navigation]);
    return <></>;
}