import ruetkitApi from "../apis/ruetkitApi";
import {toast} from "react-toastify";

export const resendPasswordVerificationCode = ({data, misc}) => async (dispatch, getState) => {
    try {
        const resend = await ruetkitApi.patch(`/api/users/${data.id}/password/reset/`);
        if (resend.status === 200) {
            await misc.setStart(true);
            setTimeout(() => {
                misc.setStart(false);
            }, 30000);
            toast.info('Resent verification code', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    } catch (e) {
        misc.setProgress(false);
        if (e.response.status === 429) {
            toast.error("You have exceeded maximum resend request", {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            toast.error(e.response.data.error.detail, {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    } finally {
        misc.setProgress(false);
    }
}