import ruetkitApi from '../apis/ruetkitApi'
import { toast } from 'react-toastify'

export const resendVerificationCode = ({ misc }) => async (dispatch, getState) => {
  misc.setProgress(true)
  const state = getState()

  try {
    const resend = await ruetkitApi.patch(`/api/users/${state.user.id}/verify/`)
    if (resend.status === 200) {
      misc.setProgress(false)
      await misc.setStart(true)
      setTimeout(() => {
        misc.setStart(false)
      }, 30000)
      toast.info('Resent verification code', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  } catch (e) {
    misc.setProgress(false)
    if (e.response.status === 429) {
      toast.error('You have exceeded maximum resend request', {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    } else {
      toast.error(e.response.data.detail, {
        position: 'bottom-left',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    }
  }
}