import ruetkitApi from '../apis/ruetkitApi'

export const fetchSiteStat = ({misc}) => async (dispatch, getState) => {
  try {
    const accessToken = localStorage.getItem('accessToken')
    const stat = await ruetkitApi.get(`/api/stat/`, {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    })
    console.log(stat.data)
    misc.setStat(stat.data)
  } catch (err) {

  }
}