import ruetkitApi from "../apis/ruetkitApi";
import {toast} from "react-toastify";
import {SET_CODE, SET_NAVIGATION, SIGNIN, VERIFY_LATER} from "./types";
import {signin} from "./signin";
import {navigate} from "./navigate";
import {signout} from "./signout";

export const resetPassword = ({data, errorHandler, misc}) => async (dispatch, getState) => {
    try {
        const user = await ruetkitApi.post(`/api/users/${data.id}/password/reset/`, {
            code: data.code,
            password: data.password,
            password2: data.password2
        });

        /* handle successful user verification */
        if (user.status === 200) {
            // misc.setProgress(false);
            // misc.setHeader("You have successfully reset your password");
            // misc.setShowPasswordFields(false);
            toast.success('Password reset successfully', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            localStorage.setItem("refreshToken", user.data.refreshToken)
            localStorage.setItem("accessToken", user.data.accessToken)
            await dispatch({
                type: SIGNIN, payload: {
                    fullname: user.data.fullname,
                    role: user.data.role
                }
            });
            toast.info(`Welcome ${user.data.fullname}`, {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            dispatch(navigate("home"));
        }
    } catch (error) {
        misc.setProgress(false);
        if (error.response.status === 401) {
            errorHandler.setPasswordError(error.response.data.error.detail);
        } else if (error.response.status === 429) {
            toast.error('You have exceeded maximum verification requests', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            toast.error('Something went wrong', {
                position: "bottom-left",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
        if (errorHandler) {
            if (error.response.data.error.field === 'password') {
                misc.setPassword2Error(error.response.data.error.detail)
            }
        }
    }
}
