import {SET_APP_PROGRESS, SIGNOUT, UNSET_APP_PROGRESS} from "./types";
import ruetkitApi from "../apis/ruetkitApi";
import {toast} from "react-toastify";
import { navigate } from './navigate'

export const signout = () => async (dispatch, getState) => {
    dispatch({type: SET_APP_PROGRESS});
    const refreshToken = localStorage.getItem("refresh");
    try {
        // const signoutRequest = await ruetkitApi.post("/api/token/blacklist/", {
        //     refresh: refreshToken
        // });
        setTimeout(() => {
            localStorage.removeItem("accessToken");
            localStorage.removeItem("refreshToken");
            dispatch({type: SIGNOUT});
            dispatch({type: UNSET_APP_PROGRESS});
            dispatch(navigate("home"));
        }, 1500);
        // if (signoutRequest.status === 200) {
        //     dispatch({type: SIGNOUT});
        //     dispatch({type: UNSET_APP_PROGRESS});
        //     dispatch(navigate("home"));
        // }
    } catch (error) {
        console.log(error)
        dispatch({type: UNSET_APP_PROGRESS})
        toast.error("Something went wrong", {
            position: "bottom-left",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
}
